.App {

  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

body {
  padding: 0;
  margin: 0;

}

.board {
  width: 500px;
  height: 500px;
  background-color: aqua;
  border: solid 1px #212121;
  display: flex;
  flex-direction: column;
}

.row {
  flex: calc(500px/3);
  display: flex;
  flex-direction: row;

}

.square {
  flex: calc(500px/3);
  height: 1005;
  border: 1px solid black;
  cursor: pointer;
  display: grid;
  place-items: center;
  font-size: 30px;
  color: black;
  font-family: Arial, Helvetica, sans-serif;
}

.square:active {
  background-color: #64f4c4;
}